
/* navigation*/
.navbar{
  position: relative;
  background-color: #E6E3E8;
  width: 100%;
  min-width: 100vh;
  min-height: 10vh;
}
.navHeader{
  position: absolute;
  left: 1%;
  top:25%;

}
.navCenter{
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;

}

.navCenter button{
font-size: x-large;
 position: absolute;
 left: 96%;
 top: 28%;
 display: none;
}

.navigation{
  position: absolute;
  left: 70%;
  top: 18%;

}

.navCenter li{
 display: inline;
 text-decoration: none;
 padding-left: 1em;
 font-size: 1.5em;
}
.navCenter li a{
  text-decoration: none;

}




@media screen and (max-width:600px){
  .navCenter button{
    display: inline;
     left: 70%;
  }
  .navigation{
    background:#E6E3E8;
    position: absolute;
    width: 40%;
    left: 50%;
    top: 70%;
    
    
  
  }

  .show-nav{
    display: show;
  }

  .hide-nav{
    display: none;
  }
  .navigation ul{
    display: flex;
    flex-direction: column;
   
  }
  .navigation li{
    margin: 1em auto 1em -2.4em ;

  }

}

/* HomePage*/
.defaultHero{
  position: relative;
  min-height: 100vh;

  background: url('../src/images/defaultBcg.jpeg');
  background-position: center;
  background-size: cover;
 
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
 
}
/* Rooms page*/

.defaultRoom{
  display: flex;
  justify-content: center;
  align-items: center;

  min-height: 60vh;
  position: relative;
  background: url('../src/images/details-1.jpeg');
}

/* banner*/
 
.banner{
 width: 60vh;
 height: 30vh;
 display: inline-block;
 background: rgba(0, 0, 0, 0.5);
 justify-content: center;
 text-transform: capitalize;
 text-align: center;
 letter-spacing: 3px;
 
}
.banner h1 {
  font-size: 2.5rem;
  color:white;
  text-transform: capitalize;
}
.banner div {
  width: 10rem;
  height: 5px;
  background: rgb(121, 80, 5);
  margin: 1.7rem auto;
}
.banner p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
  color: white;
  text-transform: capitalize;

}
.btn-primary{
  background: #db9512;
  text-align: center;
  padding: .8rem;
  box-sizing: border-box;
  border-radius: .5rem;
  color: white;


}
.banner a{
  text-decoration: none;
}

.btn-primary:hover{
  background: transparent;
  border: 3px solid #f8be52;

}
/*Servises*/

.title{
  text-align: center;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 2.5rem;
  letter-spacing: 2px;
  font-weight: 800;
}
.service-center{
  display: grid;
  
  justify-content: space-around;
  grid-template-columns: repeat(auto-fit,minmax(255px,1fr));
  grid-row-gap: 2rem;
  grid-column-gap: 50px;
}

.services{
  text-align: center;
  padding: 2rem 1rem;
}
.services span{
  font-size: xx-large;
  color: #f8be52;
}
.services h6{
  font-size: 1rem;
  text-transform: capitalize;
}
.services p{
  width: 80%;
  margin: 0 auto;

}

/*end of services */
/* featured rooms */

.featured-rooms {
  padding: 5rem 0;
}
.featured-rooms-center {
  width: 80vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
  grid-row-gap: 2rem;
  grid-column-gap: 40px;
}
@media screen and (min-width: 776px) {
  .featured-rooms-center {
    width: 90vw;
    grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
  }
}
@media screen and (min-width: 992px) {
  .featured-rooms-center {
    width: 95vw;
    max-width: 1170px;
  }
}
/* end pf featured rooms */
/* room */
.room {
  box-shadow: var(--lightShadow);
  transition: var(--mainTransition);
}
.room:hover {
  box-shadow: var(--darkShadow);
}

.img-container {
  position: relative;
}
.img-container img {
  width: 100%;
  display: block;
  transition: var(--mainTransition);
}
.price-top {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  color: var(--mainWhite);
  padding: 0.3rem 0.6rem 0.5rem;
  border-bottom-right-radius: 1rem;
  font-size: 0.5rem;
  text-align: center;
  transition: var(--mainTransition);
}
.price-top h6 {
  margin-bottom: 0;
  font-size: 0.9rem;
  font-weight: 300;
  letter-spacing: var(--mainSpacing);
}
.room-link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(0);
  transition: all 0.3s linear;
}
.img-container:hover {
  background: rgba(0, 0, 0, 0.8);
}
.img-container:hover img {
  opacity: 0.3;
}
.img-container:hover .price-top {
  opacity: 0;
}
.img-container:hover .room-link {
  transform: translate(-50%, -50%) scale(1);
}
.room-info {
  background: var(--darkGrey);
  text-transform: capitalize;
  padding: 0.5rem 0;
  text-align: center;
  font-weight: 700;
  letter-spacing: var(--mainSpacing);
}
/* end of room  */
/* single room*/

.single-room {
  padding: 5rem 0 0 0;
}
.single-room-images {
  width: 80vw;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  grid-row-gap: 2rem;
  grid-column-gap: 50px;
}
.single-room-images img {
  width: 100%;
  display: block;
}
.single-room-info {
  width: 80vw;
  display: grid;
  grid-template-columns: 1fr;
  margin: 2rem auto;
}
.desc,
.info {
  margin: 1rem 0;
}
.desc h3 {
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
}
.desc p {
  line-height: 1.5;
}
.info h3,
.info h6 {
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
}

.info h6 {
  font-weight: 300;
}
.room-extras {
  width: 80vw;
  margin: 0 auto 3rem auto;
}
.room-extras h6 {
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
}
.extras {
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(330px, 1fr));
  grid-column-gap: 2rem;
  grid-row-gap: 1rem;
}
@media screen and (min-width: 992px) {
  .single-room-images,
  .single-room-info,
  .room-extras {
    width: 95vw;
    max-width: 1170px;
  }
  .single-room-info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 2rem;
  }
  .info {
    padding-left: 3rem;
  }
}

